import { Button, CircularProgress, Typography } from "@mui/material";
import { Box } from "@mui/system";
import SelectedField from "../components/SelectedField";
import TextFieldComp from "../components/TextFieldComp";
import useAxios from "../hooks/useAxios";
import { useNavigate} from "react-router-dom";
const Settings=()=>{
    const {response,error,loading}=useAxios({url:"/api_category.php"})
    const history=useNavigate();    
    if(loading){
        return(
            <Box mt={20}>
                <CircularProgress/>
            </Box>
        )
    }
    if(error){
        return(
            <Typography variant="h6" mt={20} color="red">
                Some Went Wrong!
            </Typography>
        )
    }
    const difficultyOptions=[
        {id:'easy',name:'Easy'},
        {id:'medium',name:'Medium'},
        {id:'hard',name:'Hard'},
    ]
    const typeOptions=[
        {id:'multiple',name:'Multiple Choice'},
        {id:'boolean',name:'True/False'}
    ]
    const handleSubmit=(e)=>{
        e.preventDefault();
        history("/questions")
    }
    return(
        <div>
            <Typography variant="h2" fontWeight="bold">Quiz App</Typography>
            <form onSubmit={handleSubmit}>
                <SelectedField options={response.trivia_categories} label='Category'/>
                <SelectedField options={difficultyOptions} label='Difficulty'/>
                <SelectedField options={typeOptions} label='Type'/>
                <TextFieldComp/>
                <Box mt={3} width="100%">
                    <Button fullWidth variant="contained" type="submit">Get Started</Button>
                </Box>
            </form>
        </div>
    )
}

export default Settings;